.swiper__nav-el {
  transition: 0.25s all;
  width: 45px;
  height: 45px;
  border-radius: 42px;
  background-color: #ffffff;
  box-shadow: 0px 0px 20px 3px rgba(0, 0, 0, 0.03);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  z-index: 1;
  position: relative;
}
.swiper__nav-el::after {
  transition: 0.25s all;
  content: "";
  width: 15px;
  height: 15px;
  border: 3px solid #626263;
  border-top: none;
  border-left: none;
  display: flex;
  position: relative;
}
.swiper__nav-el--prev {
  left: 0;
}
.swiper__nav-el--prev::after {
  transform: rotate(135deg);
  left: 4px;
}
.swiper__nav-el--next {
  right: 0;
}
.swiper__nav-el--next::after {
  transform: rotate(-45deg);
  right: 3px;
}
.swiper__nav-el:hover {
  background-color: #00D78A;
}
.swiper__nav-el:hover::after {
  border-color: #ffffff;
}
.swiper__nav-el.swiper-button-disabled {
  opacity: 0;
  cursor: default;
}

